.thumpincom_box{
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 25vw;
}

.inputbox{
  position: fixed;
  bottom: 0;
  background-color: #21212C;
  width: 100%;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  height: 69px;
}
.inputForm {
  margin: 12px 20px;
  background-color: #2E323E;
  width: calc(100% - 40px);
  height: 45px;
  border-radius: 30px;
}

.text_input{
  height: 45px;
  margin-left: 20px;
}

.inputForm button{
  position: fixed;
  width: 20px;
  right: 36px;
  bottom: 24px;
}

.pin{
  position: relative;
  top: calc(50% - 16px);
  left: calc(50% - 17px);
}
.pin img {
 width: 35px;
 height: 35px;
}